import React from 'react';

// Componentes SIMPLES inline (sin imports externos)
const SimplePromoCarousel = () => (
  <div className="bg-blue-600 text-white p-6 text-center">
    <h2 className="text-2xl font-bold">ðŸŽ‰ OFERTAS ESPECIALES</h2>
    <p className="mt-2">Descuentos increÃ­bles todos los dÃ­as</p>
  </div>
);

const SimpleProductCard = ({ name, price, barcode }) => (
  <div className="bg-white p-4 rounded shadow border">
    <h3 className="font-bold text-lg">{name}</h3>
    <p className="text-blue-600 text-xl font-semibold">${price}</p>
    <p className="text-gray-500 text-sm">CÃ³digo: {barcode}</p>
    <button className="mt-3 w-full bg-green-500 text-white py-2 rounded hover:bg-green-600">
      Agregar al Carrito
    </button>
  </div>
);

const HomePage = ({ products, error }) => {
  return (
    <div className="min-h-screen bg-gray-50">
      <SimplePromoCarousel />
      
      <div className="container mx-auto px-4 py-8">
        <h1 className="text-3xl font-bold mb-6">CatÃ¡logo de Productos</h1>
        
        {error && (
          <div className="bg-red-100 text-red-700 p-4 rounded mb-8">
            <strong>Error:</strong> {error}
          </div>
        )}

        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
          {products && products.length > 0 ? (
            products.map((product) => (
              <SimpleProductCard 
                key={product.id}
                name={product.nombre}
                price={product.precio}
                barcode={product.barcode}
              />
            ))
          ) : (
            <p className="col-span-full text-center">No hay productos disponibles</p>
          )}
        </div>
      </div>
    </div>
  );
};

export async function getServerSideProps() {
  try {
    const res = await fetch('http://localhost:3000/api/productos');
    
    if (!res.ok) {
      throw new Error(`Error ${res.status}`);
    }

    const data = await res.json();
    
    return {
      props: {
        products: data.data || [],
        error: null,
      },
    };
  } catch (err) {
    return {
      props: {
        products: [],
        error: err.message,
      }
    };
  }
}

export default HomePage;